@import "normalize.css";
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  font-weight: 400;
}

a {
  text-decoration: none;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

img,
svg {
  max-width: 100%;
}

body {
  min-width: 320px;
  font-family: "Arial", sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 1.4;
  color: #121212;
  overflow: hidden;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  max-width: 1366px;
  padding: 0 170px;
  margin: 0 auto;
}

button {
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

::-moz-selection {
  color: white;
  background: rgb(185, 162, 91);
}

::selection {
  color: white;
  background: rgb(185, 162, 91);
}

.error {
  position: absolute;
  top: 100%;
  color: rgba(185, 20, 20, 0.871);
}

.popup {
  position: fixed;
  z-index: 100;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(10px);
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100%;
}
.popup__wrapper {
  width: 100%;
  max-width: 824px;
  padding: 60px;
  border-left: 1px solid rgba(255, 255, 255, 0.3);
  border-top: 1px solid rgba(255, 255, 255, 0.3);
  background-color: rgba(255, 255, 255, 0.259);
  box-shadow: 10px 10px 60px -8px rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(10px);
  border-radius: 25px;
  opacity: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.popup__span {
  font-weight: 600;
  margin-bottom: 40px;
}
.popup__text {
  margin-bottom: 40px;
}
.popup__btn {
  padding: 20px 50px;
  background-color: rgb(185, 162, 91);
  border-radius: 8px;
  font-weight: 600;
  color: white;
  transition: background-color 0.1s ease-in-out, transform 0.1s ease;
  box-shadow: 20px 20px 40px -6px rgba(0, 0, 0, 0.15);
}
.popup__btn:hover {
  background-color: #121212;
}
.popup__btn:focus-visible {
  background-color: #121212;
  outline: none;
}
.popup__btn:active {
  transform: scale(0.9);
}

.absolute {
  position: fixed;
  width: 100vw;
  height: 100vh;
  background-color: #0e0e0e;
  display: flex;
  z-index: -3;
}

.bg-blur {
  border-radius: 9999px;
  position: absolute;
  left: 0;
  top: 0;
  display: block;
  opacity: 0.5;
}

.bg-shape1 {
  border-radius: 9999px;
  position: relative;
  width: 700px;
  height: 700px;
  animation: one 18s infinite ease-in-out;
  z-index: -1;
  filter: blur(80px);
}

.bg-shape2 {
  border-radius: 9999px;
  position: relative;
  width: 500px;
  height: 500px;
  animation: two 18s infinite ease-in-out;
  z-index: -2;
  filter: blur(80px);
}

@keyframes one {
  0% {
    transform: scaleX(1) translate(0, 0);
  }
  15% {
    transform: scaleX(1.5) translate(-60px, 70px);
  }
  32% {
    transform: scaleX(1.5) translate(-30px, 30px);
  }
  46% {
    transform: scaleX(1.2) translate(40px, 70px);
  }
  52% {
    transform: scaleX(1.3) translate(20px, 150px);
  }
  78% {
    transform: scaleX(1.5) translate(50px, 100px);
  }
  90% {
    transform: scaleX(1.1) translate(20px, 30px);
  }
  100% {
    transform: scaleX(1) translate(0, 0);
  }
}
@keyframes two {
  0% {
    transform: scaleX(1) translate(0, 0);
  }
  25% {
    transform: scaleX(1.5) translate(50px, 10px);
  }
  32% {
    transform: scaleX(1.5) translate(40px, 0);
  }
  40% {
    transform: scaleX(1.2) translate(80px, 30px);
  }
  50% {
    transform: scaleX(1.3) translate(100px, 50px);
  }
  75% {
    transform: scaleX(1.5) translate(70px, 130px);
  }
  85% {
    transform: scaleX(1.1) translate(50px, 100px);
  }
  100% {
    transform: scaleX(1) translate(0, 0);
  }
}
.bg-teal {
  background: #cca73f;
}

.bg-purple {
  background: #e0d2ca;
}

.validator {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.validator .box {
  backdrop-filter: blur(10px);
  border-radius: 25px;
  border-left: 1px solid rgba(255, 255, 255, 0.3);
  border-top: 1px solid rgba(255, 255, 255, 0.3);
  transition: transform 0.25s ease-in-out;
}
.validator .box-one {
  background: rgba(255, 255, 255, 0.3);
  box-shadow: 20px 20px 40px -6px rgba(0, 0, 0, 0.3);
  position: relative;
  z-index: 5;
}
.validator .box-one:hover {
  transform: translate(1px, 1px);
}
.validator .box-one:hover .box-two {
  transform: translate(8px, 8px);
}
.validator .box-one:hover .box-three {
  transform: translate(-8px, -8px);
}
.validator .box-one:hover .box-four {
  transform: translate(-8px, 8px);
}
.validator .box-two {
  position: absolute;
  bottom: -70px;
  right: -20px;
  width: 130px;
  height: 130px;
  background: rgba(255, 255, 255, 0.3);
  box-shadow: 10px 10px 60px -8px rgba(0, 0, 0, 0.2);
}
.validator .box-three {
  position: absolute;
  top: -135px;
  right: 60%;
  width: 160px;
  height: 160px;
  background: rgba(255, 255, 255, 0.3);
  box-shadow: 10px 10px 60px -8px rgba(0, 0, 0, 0.2);
}
.validator .box-four {
  position: absolute;
  bottom: -15%;
  left: 5%;
  width: 120px;
  height: 120px;
  background: rgba(255, 255, 255, 0.3);
  box-shadow: 10px 10px 60px -8px rgba(0, 0, 0, 0.2);
}
.validator__wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.validator__rights {
  color: rgba(211, 211, 211, 0.15);
  position: absolute;
  bottom: -22%;
  left: 50%;
  transform: translateX(-50%);
  z-index: 35;
  text-align: center;
}
.validator__rights a {
  color: rgba(211, 211, 211, 0.15);
}
.validator__widget {
  padding: 40px;
}
.validator__form {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.validator__input-wrapper {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 100%;
  margin-bottom: 45px;
}
.validator__button {
  padding: 20px 50px;
  background-color: rgb(185, 162, 91);
  border-radius: 8px;
  font-weight: 600;
  color: white;
  transition: background-color 0.1s ease-in-out, transform 0.1s ease;
  box-shadow: 20px 20px 40px -6px rgba(0, 0, 0, 0.15);
}
.validator__button:hover {
  background-color: #121212;
}
.validator__button:focus-visible {
  background-color: #121212;
  outline: none;
}
.validator__button:active {
  transform: scale(0.9);
}
.validator__cards {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 40px;
}
.validator__card {
  width: 80px;
  height: 51.3px;
  margin-right: 8px;
  border-radius: 6px;
  display: flex;
}
.validator__card:last-child {
  margin-right: 0;
}
.validator__input-text {
  margin-bottom: 10px;
}
.validator__input {
  padding: 10px;
  background-color: rgba(255, 255, 255, 0.25);
  box-shadow: 10px 10px 60px -8px rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  outline: none;
  border: none;
  backdrop-filter: blur(10px);
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
  color: white;
  font-size: 25px;
  font-family: "Courier Prime", monospace;
  font-weight: 400;
  font-style: normal;
  letter-spacing: 0.2em;
  transition: background-color 0.25s ease-in-out;
}
.validator__input::placeholder {
  font-family: inherit;
  font-size: inherit;
  letter-spacing: 0.2em;
  display: flex;
  width: 100%;
  align-items: center;
  color: rgb(134, 134, 134);
}
.validator__input:focus-visible {
  background-color: rgba(255, 255, 255, 0.4);
}
.validator__img {
  border-radius: inherit;
}